<template>
  <div>
    
    <div class="container text-primary">
      <div class="h4 text-center p-2">Shopping Cart</div>
      <div class="d-flex justify-content-around p-5 text-primary  font-weight-bold ">
         <div>1.</div>
       <div >2.</div>
       <div>3.</div>
      </div>
        <table class="table text-primary">
          <thead>
            <th width="50"></th>
            <th colspan="2"></th> 
            <th width="60" class="text-center"></th>
            <th width="75" class="d-md-table-cell d-none"></th>
             <th width="75" class="d-md-table-cell d-none"></th>
            <th width="90" ></th>
            <th width="90"></th>
          </thead>
          <tbody>
            <tr >
             <tr v-for="item in CartData.carts" :key="item.id">
              <td width="42" class="align-middle">
                <!-- <div
                  class="bg-cover"
                  :style="`background-image:url('${item.product.imageUrl}');width: 42%;height:42%;`"
                ></div> -->
              </td>
              <td class="">
                 <div
                  class="bg-cover"
                  :style="`background-image:url('${item.product.imageUrl}');width: 80%;height:180px;background-size:cover; background-repeat:no-repeat;`">
                  </div>
               
              </td>
              <td class="align-middle text-left d-md-table-cell d-none ">
                <span class="text-primary ">{{item.product.title}}</span>
                 <div class="text-primary" v-if="item.coupon">
                  
                </div>
                <!-- <br>
                <span class="text-success"> : </span>
                <br>
                <span class="text-success"> : </span> -->
              </td>
              <td class="align-middle  text-right">
                <div class>{{item.qty}}</div>
              </td>
              <td class="align-middle text-right  d-md-table-cell d-none">
                <span class >{{item.product.origin_price|currency}}</span>
              </td>
               <td class="align-middle text-right">
                <span class="text-muted">{{item.product.price|currency}}</span>
              </td> 
              <td class="align-middle text-right">
                <span class>{{item.product.price * item.qty |currency}}</span>
              </td> 
                <td class="align-middle text-center">
                <button type="button"
                  class="btn btn-outline-primary btn-sm border-0"  @click="removeCartItem(item.id)">
                  <i class="far fa-trash-alt"></i>
               
                </button>
              </td>
            
            </tr>
          </tbody>
        </table>
        <hr>
        <div class="container">
        <div class="row">
      <div class="input-group col-md-6">
  
     
  </div>
  
  <div class="col-md-3 ml-md-auto text-primary">
      <div class="row justify-content-center">
   
    <div class=" ">{{1000-CartData.final_total}}</div>
   </div>
    <div class="row justify-content-center p-2">
   <div class=" col-4 "></div>
    <div class=" col-4">{{CartData.final_total|currency}}</div> 
    </div>

    <div class="row justify-content-center p-2">
   <div class=" col-4   " ></div>
    <div class=" col-4" >$80</div>
   </div>
    <div class="row justify-content-center p-2">
   <div class=" col-4 " ></div>
    <div class=" col-4"  v-if="CartData.final_total">{{CartData.final_total+80|currency}}</div>
   </div>
      <!-- <div class="row justify-content-lg-around p-2">
     <router-link type="button" class="btn btn-primary  " 
     to="cart/step2" :class="{'disabled':CartData.carts.length<1}"></router-link>
    </div>  -->
  
 </div>
</div>
 </div>
<hr>
  <div class=" container">
    <span class="col-md-3 "></span>
      <form class="col-md-6" @submit.prevent="createOrder">
       
        <div class="form-group row py-2">
          <label  class="col-sm-3 col-form-label" for="useremail"></label>
          <input type="email" class="form-control col-sm-7" name="email" id="useremail"
           :class="{'is-invalid': errors.has('email')}"
           v-model="form.user.email"
           v-validate="'required|email'"
           placeholder=" Email">
          <span class="text-primary" v-if="errors.has('email')" >
           {{errors.fist('email')}}
          </span>
        </div>

        <div class="form-group row">
          <label  class="col-sm-3 col-form-label" for="username"></label>
          <input type="text" class="form-control col-sm-7" name="name" id="username"
           :class="{'is-invalid': errors.has('name')}"
         v-model="form.user.name"
         v-validate="'required'"
          placeholder="">
          <span class="text-primary col-md-4 offset-md-3 " v-if="errors.has('name')"></span>
        </div>

   <!-- <div class=" form-group row">
            <label for="username"></label>
            <input
              type="text"
              class="form-control"
              :class="{'is-invalid':errors.has('name')}"
              name="name"
              id="username"
              v-validate="'required'"
              placeholder=""
              v-model="form.user.name"
            />
            <span class="text-danger" v-if="errors.has('name')"></span>
          </div> -->


        <div class="form-group row">
          <label  class="col-sm-3 col-form-label" for="usertel"></label>
          <input type="tel" class="form-control col-sm-7" id="usertel"
           :class="{'is-invalid': errors.has('tel')}"
          v-model="form.user.tel"
          v-validate="'required'"
           placeholder="">
            <span class="text-primary" v-if="errors.has('tel')"></span>
        </div>

        <div class="form-group row">
          <label  class="col-sm-3 col-form-label" for="useraddress"></label>
          <input type="address" class="form-control col-sm-7" name="address"
           :class="{'is-invalid': errors.has('address')}"
            v-model="form.user.address"
            v-validate="'required'"
            id="useraddress" 
            placeholder="">
          <span class="text-primary col-md-7" v-if="errors.has('address')"></span>
        </div>
        <div class="d-flex justify-content-center">
        <button class="btn btn-primary text-white mt-4 col-md-3  " @click.prevent="createOrder">
            <i class="fas fa-spinner fa-spin" v-if="status.isLoading"></i>
          </button>
        </div> 
      </form>
    </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
data(){
    return{
     CartData: {
        carts: {
         
      }
      },
    form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
      status: {
         isLoading: false, 
         currentProductId: "" 
         },
      Coupon_Code:"",
    };
},
methods:{
    getCartData() {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.$http.get(api).then(response => {
       console.log("", response);
      vm.CartData = response.data.data;
      });
    },
    removeCartItem(id) {
      const vm = this;
      //$(`#NavBarCart-${index}`).attr("disabled", true);
      vm.currentProductId = id;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.$http.delete(api).then(response => {
        console.log("", response);
        //vm.$bus.$emit("reGetCart"); //event bus
        vm.getCartData();
      });
    },
    addCouponCode(){
      const vm=this;
      vm.status.isLoading=true;
       const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
       const code={
         code: vm.Coupon_Code
       };
       vm.$http.post(api,{data:code}).then(response=>{
         if(response.data.success){
            vm.CartData.final_total = response.data.data.final_total;
            vm.$bus.$emit("message:push", response.data.message, "success");
            vm.status.isLoading = false;
        }else{
            vm.$bus.$emit("message:push", response.data.message, "danger");
             vm.status.isLoading = false;
        }
      vm.getCartData();
       });
    },

    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
        this.$validator.validate().then((result) => {
        if(result){
          this.$http.post(url,{data:order}).then((response) => {
          console.log("",response);
          if(response.data.success){
            vm.$router.push(`/customercheckout/${response.data.orderId}`);
          }
          vm.status.isLoading=false;
        });
        }else{
          console.log("");
        }
      });


    
    },
},
created(){
  this.getCartData();
}
};

</script>




